<template>
  <div>
    <div style="margin: 16px;">
      <van-button type="primary" round block @click="next">下一页</van-button>
    </div>
    <div style="margin: 16px;">
      <van-button type="primary" round block @click="gotoHome">首页</van-button>
    </div>
    <van-form>
      <van-field
        v-model="username"
        name="用户名"
        label="用户名"
        placeholder="用户名"
        :rules="[{ required: true, message: '请填写用户名' }]"
      />
      <van-field
        v-model="password"
        type="password"
        name="密码"
        label="密码"
        placeholder="密码"
        :rules="[{ required: true, message: '请填写密码' }]"
      />
    </van-form>
    <div style="margin: 16px;">
      <van-button type="primary" round block @click="login">登录</van-button>
    </div>
    <div style="margin: 16px;">
      <van-button type="primary" round block @click="logout">退出</van-button>
    </div>
  </div>
</template>

<script>
import xengine from "engine-zkty";
export default {
  name: "HelloWorld",
  components: {},
  data() {
    return {
      username: "",
      password: "",
    };
  },
  methods: {
    gotoHome() {
      xengine.navigator.gotoHome();
    },
    login() {
      if (this.username.length == 0 || this.password.length == 0) {  
        alert('请输入用户信息')
      } else {
        const params = {
          acc:this.username,
          pwd:this.password,
        }
        xengine.navigator.login(params);
      }
    },
    logout() {
      xengine.navigator.logout();
    },
    next() {
        this.$router.push({
            path:'/testB'
        })
    }
  },
};
</script>

