window.xengine = window.xengine || {}

const navigator = {
    gotoHome() {
        dsBridge.call("_navigator.gotoHome", null, (res) => { });
    },

    navigatorArgs: {},
    login(args) {
        const str = { ...this.navigatorArgs, ...args };
        dsBridge.call("_navigator.login", JSON.stringify(str), (res) => {
            console.log(res);
        });
    },
    

    logout() {
        dsBridge.call("_navigator.logout", null, (res) => { });
    }
}
module.exports = navigator;